body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.icon {
  padding: 10px;
}

.container {
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
  margin-right: auto;
  margin-left: auto;
}

.card {
  font-weight: 400;
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 0;
  word-wrap: break-word;
  background-clip: border-box;
  border: 1px solid black;
  border-radius: 0.25rem;
}

.card-body {
  margin-left: 1.5rem;
  margin-right: 1.5rem;
  flex: 1 1 auto;
  min-height: 1px;
  padding-top: 1rem;
}

.footer {
  background-color: #8fadec;
  padding-top: 1rem;
}

.button {
  width: 200px;
  height: 50px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 15px;
  letter-spacing: 2px;
  font-weight: 600;
  color: #0000;
  background-color: rgb(6, 236, 6);
  border: none;
  border-radius: 50px;
  outline: none;
}

.modal-footer {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  border-top: 1px solid #ffffff;
  border-bottom-right-radius: calc(0.3rem - 1px);
  border-bottom-left-radius: calc(0.3rem - 1px);
  margin-bottom: 0.25rem;
  margin-right: 2.5rem;
  margin-left: 2.5rem;
  padding-top: 1rem;
}

.myImg {
  padding-top: 10px;
  vertical-align: middle;
  width: 30%;
  height: 80%;
  border-radius: 80%;
}

.about-row {
  background-image: url(/my-portfolio/static/media/clouds.43b28ab2.jpg);
  height: 300px;
  background-repeat: no-repeat;
  background-size: cover;
}

.header {
  padding-top: 1rem;
  padding-left: 40px;
}

.wrap {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.portfolio-grid-item {
  text-align: left;
}

.portfolio-grid-container {
  display: grid;
  grid-template-columns: 33% 33% 33%;
}

.App-link {
  color: #61dafb;
}

@media screen and (max-width: 960px) {
  .grid-container-profile {
    grid-template-columns: repeat(2, 420px);
    grid-template-rows: repeat(3, 100px);
  }

  .header {
    padding-top: 1rem;
    padding-left: 15px;
  }

  .modal-footer {
    margin-left: 1rem;
  }
}

@media screen and (max-width: 768px) {
  .navbar a {
    float: none;
    display: block;
  }

  .grid-container-profile {
    grid-template-columns: repeat(2, 330px);
    grid-template-rows: repeat(3, 100px);
  }
}

@media screen and (max-width: 580px) {
  .myImg {
    padding-top: 10px;
    vertical-align: middle;
    width: 120px;
    height: 170px;
    border-radius: 50%;
  }

  .grid-container-profile {
    grid-template-columns: repeat(2, 180px);
    grid-template-rows: repeat(3, 120px);
  }
}

@media (min-width: 1200px) {
  .container, .container-sm, .container-md, .container-lg, .container-xl {
    max-width: 1000px !important;
  }
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

:root {
    --primary-color: #021335;
    --secondary-color: #98cbe0;
}

.grid-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(5, 120px);
    grid-gap: 10px;
    flex: 2 1 60%;
    margin: 3.5%;
    line-height: 1.5;
}

.grid-item img:hover {
    opacity: 0.5;
}

.gallery__item--1 {
    grid-column: 1/2;
    grid-row: 1/3;
}

.gallery__item--2 {
    grid-column: 1/2;
    grid-row: 2/3;
}

.gallery__item--3 {
    grid-column: 1/2;
    grid-row: 3/3;
}

.gallery__item--4 {
    grid-column: 2/2;
    grid-row: 1/ 3;
}

.gallery__item--5 {
    grid-column: 2/2;
    grid-row: 2/3;
}

.gallery__item--6 {
    grid-column: 2/2;
    grid-row: 3/3;
}

.gallery__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border: 3px solid #98cbe0;
    border: 3px solid var(--secondary-color);
}

.grid-item {
    position: relative;
}

.desc {
    background-color: rgb(9, 3, 14);
    color: #021335;
    color: var(--primary-color);
    padding: 4px 4px;
    position: absolute;
    top: 4px;
    font-size: small;
    overflow-wrap: break-word;
    width: 120px;
}

.links {
    position: absolute;
    bottom: 5px;
    font-size: small;
    overflow-wrap: break-word;
    width: 120px;
}

.desc h4 {
    font-family: 'Times New Roman', Times, sans-serif;
    font-size: small;
    color: #ffffff;
}

.desc p {
    font-size: smaller;
}

@media screen and (max-width: 960px) {
    .grid-container {
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(8, 120px);
    }
}

.gallery__item--1 {
    grid-column: 1/2;
    grid-row: 1/3;
}

.gallery__item--2 {
    grid-column: 2/2;
    grid-row: 1/ span 1;
}

.gallery__item--3 {
    grid-column: 2/2;
    grid-row: 2/ span 1;
}

.gallery__item--4 {
    grid-column: 1/1;
    grid-row: 3/ 3;
}

.gallery__item--5 {
    grid-column: 2/2;
    grid-row: 3/ span 1;
}

.gallery__item--6 {
    grid-column: 1/2;
    grid-row: 4/ span 2;
}

@media screen and (max-width: 560px) {
    .grid-container {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

.navbar {
    overflow: auto;
    padding: 0 15px;
    min-height: 9vh;
    background: rgb(44, 3, 44);
    align-items: center;
    padding: 15px 15px;
    text-decoration: none;
    font-weight: bold;
}

.navbar a {
    float: left;
    color: #ffffff;
    text-align: center;
    padding: 15px 15px;
    text-decoration: none;
    font-weight: bold;
}

.navbar-left {
    float: left;
    padding: 15px 15px;
    font-size: 25px;
}

.navbar-right {
    float: right;
    padding: 15px 15px;
    font-size: 20px;
    color: rgb(44, 3, 44)
}

.navbar a:hover {
    background-color: #17ee0f;
    color: black;
    list-style: none;
}

.navbar__link--active {
    background-color: #17ee0f;
    color: black;
    list-style: none;
}

